.template-production > #view > .prod-content {
  padding-top: 0;
}

.prod-description {

  @include media($mobile) {
    margin-bottom: 1000px;
  }

  & > .container.with-logo {
    padding-top: 8em;
  }

  .rich-text {
    padding-bottom: 2em;
  }

  .prod-logo {
    position: absolute;
    top: -75px;
    background-color: #fff;
    border: 1px solid $grey;
    left: 0;
    right: 0;
    margin: 0 auto;
  }

  .prod-team {
    width: 75%;
    position: absolute;
    left: 50%;
    @include transform(translateX(-50%));

    @include media($mobile) {
      width: 100%;
    }

  }
}
